    const currentIndex = ref(0); // Initialize currentIndex
    const dataRestored = ref(false); // Flag to check if data has been restored
    const selectedUser = ref(null);
    const selectedPhoneId = ref(null);

    // const datas = ref([{id: 1, status: 1, items:[1,2,3]}, {id:2,  status: 2, items:[1,2,3]}, {id:3,  status: 3, items:[1,2,3]}]);

    onMounted(() => {
