      }

      selectedUser.value = localStorage.getItem('user');
      selectedPhoneId.value = localStorage.getItem('phoneId');
    });

    // Watch currentIndex and save it to localStorage whenever it changes
    watch(currentIndex, (newIndex) => {
      if (newIndex === null) {
        const savedIndex = localStorage.getItem('currentIndex');
        if (savedIndex !== null) {
          currentIndex.value = parseInt(savedIndex, 10);
