      const newLogEntry = {
        name: this.currentContact.name,
        phoneNumber: this.currentContact.phoneNumber,
        time: new Date().toLocaleString() // Get current date and time
      };

      // Add the new log entry to the call log
      this.callLog.push(newLogEntry);

      // Keep only the latest 12 entries in the log
      if (this.callLog.length > 8) {
        this.callLog.shift(); // Remove the oldest entry if more than 12
      }
    }

    async function makeCall(contact, selectedUser, currentIndex, contacts) {
      try {
        //  await new Promise(resolve => setTimeout(resolve));
        let { name, doc, phoneNumber, status, interaction, lastCall, nextCall, no } = contact;
        lastCall = convertDMY2YMDtime(lastCall);
        nextCall = convertDMY2YMDtime(nextCall);

        let user = selectedUser;
        let id = 0;
        if (user.toLowerCase() === "alyssa") id = 3;
        if (user.toLowerCase() === "alvin") id = 4;
        if (user.toLowerCase() === "ashton") id = 2;
        const variables =
          { id: id, doc: doc, user: user, name: name, phoneNumber: phoneNumber, status: status, interaction: interaction, lastCall: lastCall, nextCall: nextCall, no: no }
        await updateCallme(variables);
      } catch (error) {
        alert("error 100");
        console.error('Error updating callme:', error);
        // Handle error appropriately
      }
      currentIndex = contacts.findIndex(c => c.no === contact.no);
    }



    const restoreDataFromLocalStorage = () => {
      console.log('Attempting to restore data from local storage...');
      const savedData = localStorage.getItem('appData');
      if (savedData) {
