          const parsedData = JSON.parse(savedData);
          contacts.value = parsedData.contacts || [];
          // Restore other properties as needed
          dataRestored.value = true;
          console.log('Data restored from local storage:', parsedData);
        } catch (error) {
          alert("error 101"); console.error
          console.error('Error parsing data from local storage:', error);
        }
      } else {
        console.log('No saved data found in local storage.');
      }
    };

    onMounted(() => {
      restoreDataFromLocalStorage();

