      // contacts: [
      //   { no: 1, project: 'abc', source: 'uom', name: 'John', phoneNumber: '+601116331800', interaction: 'hello', message: '!ei2', status: '', stage: "w", date: "10-10-2024", email: "aa@bb.com", occupation: "IT", lastCall: "10-10-2024", nextCall: "10-10-2024" },
      //   { no: 2, project: 'abc', source: 'uom', name: 'Jane', phoneNumber: '+601116331801', interaction: '', message: '!ei2', status: '', stage: "w", date: "10-10-2024", email: "aa@bb.com", occupation: "IT", lastCall: "10-10-2024", nextCall: "10-10-2024" },
      //   { no: 3, project: 'abc', source: 'uom', name: 'Alex', phoneNumber: '+601116331802', interaction: '', message: '!ei2', status: '', stage: "w", date: "10-10-2024", email: "aa@bb.com", occupation: "IT", lastCall: "10-10-2024", nextCall: "10-10-2024" }
      //   // Add more contacts as needed
      // ],
      totalContacts: 0, // Total number of contacts to sync
      updatedCount: 0, // Number of updated contacts
      progressPercentage: 0, // Percentage of records updated
    };
  },
  computed: {
    currentContact() {
      return this.contacts[this.currentIndex] || {};
    },


    filteredContacts() {
      if (this.filterTerm === '') {
        return this.contacts; // Show all records
      } else if (this.filterTerm.toLowerCase() === 'dup') {
        return this.getDuplicateRecords(); // Show only duplicate records
      }
      else if (this.filterTerm.toLowerCase() === 'r') {
        return this.contacts.filter(contact => contact.rating > 0); // Show records with '*' in status
      }
      else {
        return this.contacts.filter(contact => {
          // Define the specific fields you want to search through
          const fieldsToSearch = [contact.name, contact.phoneNumber, contact.status, contact.interaction]; // Add more fields if needed
          return fieldsToSearch.some(value =>
            value.toString().toLowerCase().includes(this.filterTerm.toLowerCase())
          );
        });
      }
    },
  },



  methods: {
 updateCallCount() {
      if (!isPhoneDisabled(this.currentContact)) {
        // Update the 'called' field in the contact
        this.currentContact.called = (function (calledString) {
          // Attempt to convert the string to a number
          const numberValue = parseInt(calledString, 10);

          // Check if conversion was successful (is a number)
          if (!isNaN(numberValue)) {
            return numberValue + 1; // Increment the number
          } else {
            return 1; // Set to 1 if not numeric
          }
        })(this.currentContact.called);

        // Optionally, save or send updated data to the server
        // this.updateContact(this.currentContact); // Example save method
      }
      this.currentContact.dialedDate = this.todayDDMMYYYY();
    },


