    getLast8Characters(phoneNumber) {
      // Remove any non-digit characters using a regular expression
      const cleanedNumber = phoneNumber.replace(/\D/g, '');

      // Return the last 8 characters of the cleaned number
      return cleanedNumber.slice(-8);
    },



    updateLocalCopy(contact, existingContact) {
      const index = this.contacts.indexOf(contact);
      if (index > -1) {
        this.contacts[index] = existingContact;
      }
    },
    onFileSelected(event) {
      this.file = event.target.files[0];
      if (!this.file) {
        alert("error 103");
        console.error('No file selected');
        return;
      }

      // Show the date format dialog after the file is selected
      this.showDateFormatDialog = true;
    },
    confirmStatus() {
      // Ensure current contact exists and status is selected
      if (this.contacts[this.currentIndex] && this.selectedStatus) {
        // Append the selected status to the current status of the contact
        this.contacts[this.currentIndex].status = this.selectedStatus + ' ' + this.contacts[this.currentIndex].status;
        if (this.selectedStatus == ",") {
          this.CallSuccess(this.contacts[this.currentIndex]);
        }
        // Move to the next contact
        this.nextContact();

        // Close the dialog
        this.showStatusDialog = false;
      } else {
        alert("error 104");
        console.error('Contact or selected status is undefined.');
      }
    },
    todayDDMMYYYY() {
      const date = new Date();
      const day = String(date.getDate()).padStart(2, '0'); // Add leading zero if needed
      const month = String(date.getMonth() + 1).padStart(2, '0'); // Months are 0-indexed
      const year = date.getFullYear();

      return `${day}/${month}/${year}`;
    },
   
    clearForm() {
      // Clear the contact form (reset data)
      const highestNo = Math.max(...this.contacts.map(contact => contact.no || 0)) + 1;

      this.contacts[this.currentIndex] = {
        name: '',
        phoneNumber: '',
        interaction: '',
        project: this.currentContact.project,
        source: this.currentContact.source,
        updatedAt: new Date().toISOString(),
        no: highestNo,
        user: this.selectedUser,
        phoneId: this.selectedPhoneId
        // Add other fields as needed
      };
    },
    addContact() {
      this.contacts.push({ ...this.currentContact });
      this.clearForm();
