    deleteContact() {
      const confirmation = confirm(`Are you sure you want to delete the contact: ${this.contacts[this.currentIndex].name} (${this.contacts[this.currentIndex].phoneNumber})?`);

      // If the user confirms, proceed with the deletion
      if (confirmation) {
        this.contacts.splice(this.currentIndex, 1);
      }
      // this.clearForm();
    },
    // Navigate to next or previous contacts

    navigateToTalk(contact) {
      //   this.$router.push({ path: '/talk', query: { contact } });
      this.$router.push({ path: '/talk', query: { contact: JSON.stringify(contact) } });
    },
    CallSuccess(contact) {
      const today = new Date(); // Ensure `today` is defined
      const todayDate = `${today.getDate()}/${today.getMonth() + 1}/${today.getFullYear()}`;
      contact.lastCall = this.convertDMY2YMD(todayDate, true);
      contact.contacted += 1;

    },

    isValidEmail(email) {
      // Regular expression for validating an email address
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return emailRegex.test(email);
    },
    previousContact() {
      if (this.currentIndex > 0) {
        this.currentIndex--;
      }
    },
    nextContact() {
      if (this.currentIndex < this.contacts.length - 1) {
        this.currentIndex++;
      }
    },
    AInextContact() {
      this.currentIndex++;
      while (this.currentIndex < this.contacts.length - 1 && (this.contacts[this.currentIndex].status.includes("ni") || this.contacts[this.currentIndex].status.includes("xxx") || this.contacts[this.currentIndex].called <= 3)) {
        this.currentIndex++;
      }
    },
    starNextContact() {
      this.currentIndex++;
      while (this.currentIndex < this.contacts.length - 1 && (!this.contacts[this.currentIndex].status.includes("*") || !this.contacts[this.currentIndex].interaction.includes("*"))) {
        this.currentIndex++;
      }
    },
    isBlank(ss) {
      return ss === null || ss === undefined || ss.trim() === '';
    },
    formattedPhoneNumber(ss) {
      if (this.isBlank(ss)) return '';

      // Extract country code and local number
      const countryCode = ss.substring(0, 3);
      const localNumber = ss.substring(3);

      // Format the phone number
      return `(${countryCode}) ${localNumber}`;
    },
    // openDialog(existingContact, contact) {
    //   this.existingContact = existingContact;
    //   this.contact = contact;
    //   this.dialog = true;
    // },

    async showConfirmDialog(existingContact, contact, index) {
      console.log('Showing confirm dialog...');
      return new Promise((resolve) => {
        this.showSynDialog = true; // Show the dialog
        this.showRecordNo = index;
        this.existingContact = existingContact;
        this.contact = contact;

        // Function to handle user choice
        this.confirmSyn = (choice) => {
          console.log(`User choose: ${choice}`);
          // if (choice == "local") {
          //   this.deleteLocalCopy(contact);
          // } else 
          if (choice == "deleteServerCopy") {
            this.deleteServerAddLocalCopy(existingContact);
          }
          else if (choice == "updateServerAllCopy") {
            this.isCopyALL = true;
            this.deleteServerAddLocalCopy(existingContact);
          } else if (choice == "updateServerCopy") {
            this.deleteServerAddLocalCopy(existingContact);
          } else if (choice == "updateLocalCopy") {
            this.updateLocalCopy(contact, existingContact);
          }
          this.showSynDialog = false; // Hide the dialog
          resolve(choice); // Resolve the promise with the user's choice
        };

        // Function to handle cancel action
        this.cancelCompare = () => {
          console.log('User canceled the dialog.');
          this.showSynDialog = false; // Hide the dialog
          resolve(false); // Resolve with false to indicate cancellation
        };
      });
