    async loadContactsFromStrapi(projectFilter) {
      const backupConfirmation = confirm('Please backup data before you download new data. Click "OK" to proceed, or "Cancel" to quit.');

      // If the user cancels, stop the process
      if (!backupConfirmation) {
        return; // Exit the function if the user clicks "Cancel"
      }

      const action = confirm('Do you want to overwrite existing contacts or append the new ones? Click "OK" to overwrite or "Cancel" to append.');

      // If the user selects "OK", clear existing contacts
      if (action) {
        this.contacts = [];
      }
      loadByProjectFunc(projectFilter, this.contacts);
