    convertDate(dateString) {
      if (!dateString) return null;

      // Convert based on the selected format
      if (this.selectedDateFormat === 'dd/mm/yyyy') {
        return convertDMY2YMDtime(dateString);
      } else if (this.selectedDateFormat === 'mm/dd/yyyy') {
        return this.convertMDY2DMY(dateString);
      }
    },
    convertMDY2DMY(dateString) {
      // Check if dateString is valid
      if (!dateString) return null;

      // Replace different delimiters with '/'
      const formattedDateString = dateString.replace(/-/g, '/');

      // Split the date string into components
      const [month, day, year] = formattedDateString.split('/').map(Number);

      // Validate date components
      if (isNaN(month) || isNaN(day) || isNaN(year)) {
        alert("error 106");
        console.error('Invalid date components');
        return null;
      }

      // Create a new Date object to verify the date
      const date = new Date(year, month - 1, day); // Month is zero-based in JavaScript

      // Check if the date was created correctly
      if (date.getFullYear() !== year || date.getMonth() !== month - 1 || date.getDate() !== day) {
        alert("error 107");
        console.error('Invalid date');
        return null;
      }

      // Format the date as DD/MM/YYYY
      const dayStr = String(day).padStart(2, '0');
      const monthStr = String(month).padStart(2, '0');
      const yearStr = String(year);

      return `${dayStr}/${monthStr}/${yearStr}`;
    },
    convertMDY2YMD(dateString) {
      const formattedDateString = dateString.replace(/-/g, '/');
      const [month, day, year] = formattedDateString.split('/').map(Number);

      if (isNaN(day) || isNaN(month) || isNaN(year)) {
        alert("error 108");
        console.error('Invalid date components');
        return null;
      }

      const date = new Date(year, month - 1, day);
      if (date.getFullYear() !== year || date.getMonth() !== month - 1 || date.getDate() !== day) {
        alert("error 109");
        console.error('Invalid date');
        return null;
      }

      return formatDateToYMD(date);
    },

