    async checkContactConflict(existingContact, contact, index) {
      if (existingContact.stage === null)
        existingContact.stage = "";
      if (
        existingContact.source === contact.source &&
        existingContact.name === contact.name &&
        existingContact.project === contact.project &&
        existingContact.interaction === contact.interaction &&
        existingContact.status === contact.status &&
        existingContact.phoneId === contact.phoneId &&
        existingContact.stage === contact.stage &&
        existingContact.rating == contact.rating &&
        existingContact.no === contact.no &&
        index <= 1 &&
        existingContact.phoneNumber === formatPhoneNumber2(contact.phoneNumber)
      ) {
        return false;

      }

      return await this.showConfirmDialog(existingContact, contact, index);
    },
    checkContactConflictAll(existingContact, contact) {
      if (
        existingContact.source !== contact.source ||
        existingContact.name !== contact.name ||
        existingContact.project !== contact.project ||
        existingContact.interaction !== contact.interaction ||

        existingContact.phoneId !== contact.phoneId
      ) {
        this.deleteServerAddLocalCopy(existingContact);
        return "updateServerCopy";
      }
      return false;
    },
    async uploadToStrapi() {

      const updateMutation = gql`
    mutation UpdateLead(
      $id: ID!
      $no: Int
      $user: String
      $name: String
      $phoneNumber: String
      $status: String
      $phoneId: String
      $interaction: String
      $lastCall: DateTime
      $nextCall: DateTime
      $called: Int
      $rating: Int
      $contacted: Int
      $language: String
      $message: String
      $doc: String
      $stage: String
      $source: String
      $project: String
      $email: String
      $dialedDate: DateTime
      $sourceDate: Date
    ) {
      updateLead(
        id: $id
        data: {
          user: $user
          name: $name
          phoneNumber: $phoneNumber
          status: $status
          phoneId: $phoneId
          interaction: $interaction
          lastCall: $lastCall
          nextCall: $nextCall
          called: $called
          rating: $rating
          no: $no
          language: $language
          contacted: $contacted
          message: $message
          doc: $doc
          stage: $stage
          source: $source
          project: $project
          email: $email
          dialedDate: $dialedDate
          sourceDate: $sourceDate
        }
      ) {
        data {
          id
          attributes {
            name
            phoneNumber
            status
            interaction
            lastCall
            nextCall
            no
          }
        }
      }
    }
  `;

      this.totalContacts = this.contacts.length;
      this.updatedCount = 0; // Reset count at the start

      try {
        for (const contact of this.contacts) {
          if (contact.phoneNumber.length <= 8) {
            alert("Name:" + contact.name + ",Phone Number:" + contact.phoneNumber + " Phone number less then 8 not allow");
            return;
          }
          const existingContactQuery = gql`
            query GetLead($phoneNumber: String!) {
              leads(filters: { phoneNumber: { contains: $phoneNumber } }) {
                data {
                  id
                  attributes {
                    no
                    source
                    project
                    user
                    name
                    phoneNumber
                    status
                    phoneId
                    interaction
                    lastCall
                    nextCall
                    called
                    rating
                    contacted
                    language
                    message
                    doc
                    stage
                    updatedAt
                    email
                    dialedDate
                    sourceDate
                  }
                }
              }
            }
          `;



          if ((this.importDate < contact.updatedAt || contact.updatedAt == null) && !this.isBlank(contact.phoneNumber)) {
            const { data } = await client.query({
              query: existingContactQuery,
              variables: { phoneNumber: this.getLast8Characters(contact.phoneNumber) },
              fetchPolicy: 'no-cache',
            });

            this.updatedCount++;


            if ((Array.isArray(data.leads.data) && data.leads.data.length > 0)) {
              //for (const existingLead of data.leads.data) {
              for (let i = 0; i < data.leads.data.length; i++) {
                // const existingContact = existingLead.attributes;
                // existingContact.id = existingLead.id;
                const existingLead = data.leads.data[i];
                const existingContact = existingLead.attributes;
                existingContact.id = existingLead.id;
                // Check for conflicts
                var hasConflict = false;
                if (this.isCopyALL)
                  hasConflict = this.checkContactConflictAll(existingContact, contact);
                else {
                  hasConflict = await this.checkContactConflict(existingContact, contact, data.leads.data.length - i);
                  if (hasConflict == false) {
                    continue;
                  }
                }

                if (hasConflict == 'updateServerCopy' || hasConflict == 'updateServerAllCopy') {
                  //  check record still exist in server
                  const exists = await this.recordExists(formatPhoneNumber2(contact.phoneNumber), existingContactQuery);

                  if (exists) {
                    continue; // Skip this record if there's a conflict
                  }
                  else // if no more extra record in server, then add local copy
                  {
                    contact.user = this.selectedUser;
                    if (this.selectedPhoneId.trim() != "*")
                      contact.phoneId = this.selectedPhoneId;
                    this.AddRecord(contact);
                  }
                }
                else if (hasConflict == 'deleteServerCopy') {
                  // do nothing
                }

                if (hasConflict != false) {
                  console.log(`Conflict detected for ${contact.phoneNumber}. Skipping...`);
                  continue; // Skip this record if there's a conflict
                }

                // just update server record
                const fieldsToUpdate = {};
                for (const field in contact) {
                  if (contact[field] !== existingContact[field] && field !== 'id') {
                    fieldsToUpdate[field] = contact[field];
                  }
                }

                if (Object.keys(fieldsToUpdate).length > 0) {
                  try {
                    await client.mutate({
                      mutation: updateMutation,
                      variables: {
                        id: existingContact.id,
                        ...fieldsToUpdate,
                        contacted: parseInt(contact.contacted, 10),
                        called: parseInt(contact.called, 10),
                        rating: parseInt(contact.rating, 10),
                        dialedDate: convertDMY2YMDtime(contact.dialedDate, true),
                        sourceDate: convertDMY2YMDtime(contact.sourceDate, false),
                        email: this.isValidEmail(contact.email) ? contact.email : null, // Conditional check for email
                        lastCall: convertDMY2YMDtime(contact.lastCall, true),
                        nextCall: convertDMY2YMDtime(contact.nextCall, true),
                        phoneNumber: formatPhoneNumber2(contact.phoneNumber),
                        user: this.selectedUser,
                        phoneId: this.selectedPhoneId
                      },
                    });
                    console.log(`Updated contact: ${contact.phoneNumber}`);
                  } catch (updateError) {
                    alert("error 114");
                    console.error(`Error updating contact ${contact.phoneNumber}:`, updateError);
                  }
                }
              }


            } else {
              // If no existing contact found, create a new one
              contact.user = this.selectedUser;
              if (this.selectedPhoneId.trim() != "*")
                contact.phoneId = this.selectedPhoneId;
              this.AddRecord(contact);
            }

            this.progressPercentage = (this.updatedCount / this.totalContacts) * 100;
            console.log(`Progress: ${this.progressPercentage.toFixed(2)}%`);
          }
        }
        alert('Data synchronization completed!');
        this.isCopyALL = false;
      } catch (error) {
        alert("error 115");
        console.error('Error syncing contacts with Strapi:', error);
        alert('Error syncing contacts with Strapi: ' + error.message);
      }
    },



    async recordExists(phoneNumber) {
      try {
        // Execute the query
        const { result } = useQuery(EXISTING_CONTACT_QUERY, { phoneNumber });

        // Wait for the result
        const queryResult = await result;

        // Safely access the data
        const data = queryResult.value || {};
        const leadsData = data.leads || {};
        const leads = leadsData.data || [];

        // Debugging: log the data to see what is returned
        console.log('Query result:', data);

        // Check if any records are returned
        return leads.length > 0;
      } catch (error) {
        alert("error 116");
        console.error('Error checking record existence:', error);
        return false; // Handle error as needed
      }
    },


    async AddRecord(contact) {
      const createMutation = gql`
        mutation CreateLead(
          $no: Int
          $user: String
          $name: String
          $phoneNumber: String
          $status: String
          $phoneId: String
          $interaction: String
          $lastCall: DateTime
          $nextCall: DateTime
          $called: Int
          $rating: Int
          $contacted: Int
          $language: String
          $message: String
          $doc: String
          $stage: String
          $source: String
          $project: String
          $email: String
          $dialedDate: DateTime
          $sourceDate: Date
        ) {
          createLead(
            data: {
              user: $user
              name: $name
              phoneNumber: $phoneNumber
              status: $status
              phoneId: $phoneId
              interaction: $interaction
              lastCall: $lastCall
              nextCall: $nextCall
              called: $called
              rating: $rating
              no: $no
              language: $language
              contacted: $contacted
              message: $message
              doc: $doc
              stage: $stage
              source: $source
              project: $project
              email: $email
              dialedDate: $dialedDate
              sourceDate: $sourceDate
            }
          ) {
            data {
              id
              attributes {
                name
                phoneNumber
                status
                phoneId
                interaction
                lastCall
                nextCall
                no
                project
                source
              }
            }
          }
        }
      `;

      try {
        const response = await client.mutate({
          mutation: createMutation,
          variables: {
            ...contact,
            no: parseInt(contact.no, 10),
            contacted: contact.contacted ? parseInt(contact.contacted, 10) : 0,
            called: contact.called ? parseInt(contact.called, 10) : 0,
            rating: contact.rating ? parseInt(contact.rating, 10) : 0,
            dialedDate: convertDMY2YMDtime(contact.dialedDate, true),
            sourceDate: convertDMY2YMDtime(contact.sourceDate, false),
            email: this.isValidEmail(contact.email) ? contact.email : null,
            lastCall: convertDMY2YMDtime(contact.lastCall, true),
            nextCall: convertDMY2YMDtime(contact.nextCall, true),
            phoneNumber: formatPhoneNumber2(contact.phoneNumber),
            // user: this.selectedUser,
            // phoneId: this.selectedPhoneId
          },
        });

        console.log(`Created new contact: ${contact.phoneNumber}`);
        console.log('Response:', response.data.createLead.data);
      } catch (createError) {
        alert("error 117");
        console.error(`Error creating contact ${contact.phoneNumber}:`, createError);
        // Optionally, you can extract more details from createError to log specific issues
        if (createError.graphQLErrors) {
          createError.graphQLErrors.forEach(error => {
            alert("error 118");
            console.error('GraphQL Error:', error.message);
          });
        }
        if (createError.networkError) {
          alert("error 119");
          console.error('Network Error:', createError.networkError.message);
        }
      }
    },
    removeContact(index) {
      // Get the contact from the filtered list
      const contactToRemove = this.filteredContacts[index];

      // Confirm with the user before deleting
      const confirmation = confirm(`Are you sure you want to delete the contact: ${contactToRemove.name} (${contactToRemove.phoneNumber})?`);

      // If the user confirms, proceed with the deletion
      if (confirmation) {
        // Find the index of this contact in the original contacts array
        const originalIndex = this.contacts.findIndex(contact => contact === contactToRemove);

        // Debugging: Log the original index and the contact to be removed
        console.log('Removing contact at original index:', originalIndex);
        console.log('Contact:', this.contacts[originalIndex]);

        // Remove the contact from the original contacts array if it exists
        if (originalIndex !== -1) {
          this.contacts.splice(originalIndex, 1);
        }
      } else {
        // If the user cancels, log a message (optional)
        console.log('Contact removal canceled.');
      }
    },
    getDuplicateRecords() {
      const phoneNumberCount = this.contacts.reduce((acc, contact) => {
        const phoneNumber = contact.phoneNumber.trim();
        if (phoneNumber) {
          acc[phoneNumber] = (acc[phoneNumber] || 0) + 1;
        }
        return acc;
      }, {});

      const duplicateNumbers = Object.keys(phoneNumberCount).filter(number => phoneNumberCount[number] > 1);

      return this.contacts.filter(contact => duplicateNumbers.includes(contact.phoneNumber.trim()));
    },
    stopCountdownFromParent() {
      this.$refs.countdown.stopCountdown();
    },
    async sendMessage() {
      this.currentIndex = 0;
      while (this.currentIndex < this.contacts.length) {
        // Check if the current contact has already been sent a message
        //   if (this.contacts[this.currentIndex].status.trim() !== 'send' && this.contacts[this.currentIndex].phoneNumber.trim().length > 0) {

        if (
          !this.contacts[this.currentIndex].status.trim().toLowerCase().includes('send') &&
          !this.contacts[this.currentIndex].status.trim().toLowerCase().includes('xxx') &&
          this.contacts[this.currentIndex].phoneNumber.trim().length > 0
        ) {

          const contact = this.contacts[this.currentIndex];
          let language = "e";

          if (contact.language !== undefined && contact.language.trim() !== "") {
            language = contact.language;
          }


          try {
            // if (contact.status.trim().length > 0) {
            //   alert(contact.status + ":" + contact.phoneNumber);

            // }

            let message = "!" + language + this.sendmsg;
            let msg = this.getmessage(message, contact);
            contact.status = "send";
            if (contact.doc !== undefined)
              contact.doc = this.sendmsg + contact.doc;
            else
              contact.doc = this.sendmsg


            //      alert("calling :" + contact.name + ":" +  contact.phoneNumber + ":"+ msg);

            const response = await axios.post('http://localhost:3000/send-message', {
              number: contact.phoneNumber,
              message: msg
            });

            if (response.data.success) {
              // Optionally handle successful message sending
            } else {
              alert('Failed to send message: ' + response.data.error);
              contact.status = "error 1 send";
              this.$refs.countdown.stopCountdown();
              return; // Exit the function if message sending fails
            }
          } catch (error) {
            alert("error 120");
            console.error('Error:', error);
            contact.status = "error 2 send";
            alert('An error occurred: ' + error.message);
            this.$refs.countdown.stopCountdown();
            return; // Exit the function if an error occurs
          }
          this.currentIndex++;
          break;
        }
        // Move to the next contact
        this.currentIndex++;

      }

      if (this.currentIndex >= this.contacts.length) {
        this.$refs.countdown.stopCountdown();
        alert("send whatapp message completed");
      }
    },

    isVisible(contact) {
      if (this.filter === 'showAll') {
        return true; // Show all records
      }
      // Your existing filter logic
      // Example:
      return contact.phoneNumber && contact.phoneNumber.includes(this.searchTerm);
    },
    countdownFinished() {
      console.log("Countdown finished!");
      this.sendMessage();
      // You can add any additional logic you need here
    },
    sortContacts(property) {
      const sortDirection = this.sortOrder[property] === 'asc' ? 1 : -1;

      this.contacts.sort((a, b) => {
        if (a[property] < b[property]) return -1 * sortDirection;
        if (a[property] > b[property]) return 1 * sortDirection;
        return 0;
      });

      // Toggle sort order
      this.sortOrder[property] = this.sortOrder[property] === 'asc' ? 'desc' : 'asc';
    },


    renumbering() {
      // Sort contacts by 'name'
      this.contacts.sort((a, b) => {
        if (a['name'] < b['name']) return -1;
        if (a['name'] > b['name']) return 1;
        return 0;
      });

      // Renumber the 'no' field for each contact after sorting
      this.contacts.forEach((contact, index) => {
        contact.no = index + 1; // Assign new number starting from 1
        contact.updatedAt = new Date().toISOString();
      });
    }
    ,
