<template>
  <div class="slider-container">
    <div class="slider-wrapper" :style="{ transform: `translateX(-${currentIndex * 100}%)` }">
      <div class="slider-slide" v-for="(image, index) in images" :key="index">
        <img :src="image.src" :alt="`Slide ${index + 1}`" style="width: 100%;">
        <!-- Add button to navigate -->
        <button class="navigate-btn" @click="navigateTo(image.route,image.category)">
          Go to {{ image.route }}
        </button>
      </div>
    </div>
    <button class="prev-btn" @click="prevSlide"></button>
    <button class="next-btn" @click="nextSlide"></button>
  </div>
</template>

#### **Script**
```javascript
<script>
export default {
  name: 'MySlider',
  props: {
    pageTitle: {
      type: String,
      default: ''
    },
    images: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      currentIndex: 0,
      intervalId: null
    };
  },
  mounted() {
    this.startAutoPlay();
  },
  beforeUnmount() {
    this.stopAutoPlay();
  },
  methods: {
    startAutoPlay() {
   //   this.intervalId = setInterval(this.nextSlide, 3000); // Change image every 3 seconds
    },
    stopAutoPlay() {
      clearInterval(this.intervalId);
    },
    prevSlide() {
      this.stopAutoPlay();
      this.currentIndex = (this.currentIndex - 1 + this.images.length) % this.images.length;
      this.startAutoPlay();
    },
    nextSlide() {
      this.stopAutoPlay();
      this.currentIndex = (this.currentIndex + 1) % this.images.length;
      this.startAutoPlay();
    },
    // navigateTo(route,param) {
    //   this.$router.push(route,param);
    // }
    

    navigateTo(route, param) {
    this.$router.push({ path: route, query: { param: param } });
  }
  }
};
</script>


<style scoped>
.slider-container {
  position: relative;
  max-width: 800px;
  margin: 0 auto;
  overflow: hidden;
  width: 100%;
}

.slider-wrapper {
  display: flex;
  transition: transform 0.5s ease-in-out;
  width: 100%;
}

.slider-slide {
  min-width: 100%;
  box-sizing: border-box;
  position: relative; /* Ensure positioning for child elements */
}

.navigate-btn {
  position: absolute;
  bottom: 20px; /* Adjust as needed */
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  z-index: 2;
}

.prev-btn, .next-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  z-index: 1;
}

.prev-btn {
  left: 10px;
}

.next-btn {
  right: 10px;
}
</style>
