import { createRouter, createWebHashHistory } from 'vue-router'
import HomePage from '../components/HomePage.vue'
import AboutPage from '../components/AboutPage.vue'
import BusinessPage from '../components/BusinessPage.vue'
import WinbackPage from '../components/WinbackPage.vue'
import MainPage from '../components/MainPage.vue'
import ContactList from '../components/ContactList.vue'
import SocketComponent from '../components/SocketComponent.vue';
import listByProject from '../components/ProjectMaintenance.vue';
import saleTalk from '../components/saleTalk.vue';
import listProject from '../components/listProject.vue';

const routes = [
  {
    path: '/main',
    name: 'MainPage',
    component: MainPage,
    meta: { requiresAuth: false }
  },
  {
    path: '/home',
    name: 'HomePage',
    component: HomePage,
    meta: { requiresAuth: false }
  },
  {
    path: '/soc',
    name: 'SocketComponent',
    component: SocketComponent,
    meta: { requiresAuth: false }
  },

  {
    path: '/snr',
    name: 'ContactList',
    component: ContactList,
    meta: { requiresAuth: false }
  },
   {
    path: '/',
    name: 'listByProject',
    component: listByProject,
    meta: { requiresAuth: false }
  },
  {
    path: '/talk',
    name: 'saleTalk',
    component: saleTalk,
    meta: { requiresAuth: false }
  },
  {
    path: '/listProject',
    name: 'listProject',
    component: listProject,
    meta: { requiresAuth: false }
  },
  {
    path: '/business',
    name: 'BusinessPage',
    component: BusinessPage,
    meta: { requiresAuth: false }
  },
  {
    path: '/winback',
    name: 'WinbackPage',
    component: WinbackPage,
    meta: { requiresAuth: false }
  },
  {
    path: '/about',
    name: 'AboutPage',
    component: AboutPage,
    meta: { requiresAuth: false }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

// Route guard
router.beforeEach((to, from, next) => {
  const isAuthenticated = localStorage.getItem('authenticated') === 'true';
  if (to.matched.some(record => record.meta.requiresAuth) && !isAuthenticated) {
    next('/');
  } else {
    next();
  }
});

export default router
