  // Social Media & Marketing Campaigns
  { label: '-Social Media & Marketing Campaigns', value: 'M', disabled: true },
  { label: '1 MCP: Initial campaign call', value: 'CAC' },
  { label: '2 MCF: Follow-up on social media campaign', value: 'CAF' },
  { label: '3 MCV: Post-showroom client follow-up', value: 'CSV' },

  // Social Media & Marketing Campaigns
  { label: '-Social Media & Marketing Campaigns', value: 'M', disabled: true },
  { label: '1 BCB: Look for Co Broke Project, which need IProperty Advertisement', value: 'CAC' },
  { label: '3 BI: Collect photo, land size, room & bathroom details', value: 'SI' },
  { label: '4 BP: On-site visit to take photo/video', value: 'SP' },
  { label: '2 BA: Please Advertisement', value: 'CAF' },
  { label: '8 BQ: Follow-up Customer Enquiries', value: 'SF' },
  { label: '5 BVF: Post-Site Visit follow-up', value: 'CSV' },

  // Subsales Stages: Sales Process
  { label: '-Subsales Stages: Sales', value: 'S', disabled: true },
  { label: '1 SC: Initial call to collect rental/sales info', value: 'SC' },
  { label: '2 SI: Collect photo, land size, room & bathroom details', value: 'SI' },
  { label: '3 SP: On-site visit to take photo/video', value: 'SP' },
  { label: '4 SS: Suvey surrounding area, and write highlight on the area)', value: 'SV' },
  { label: '5 SB: Bank evaluation & market price research', value: 'SB' },
  { label: '6 SD: Document information', value: 'SD' },
  { label: '7 SA: Post advertisement on Property Guru/Mudah', value: 'SA' },
  { label: '8 SQ: Follow-up customer enquiries', value: 'SF' },
  { label: '9 SV: Arrange customer viewing', value: 'SV' },
