  { label: '14 SLA: Wait for loan approval', value: 'SLA' },
  { label: '15 SKY: Pass key to customer', value: 'SKY' },
  { label: '16 SCL: Subsale process completed', value: 'SCL' },

  // Subsales Stages: Rental Process
  { label: '-Subsales Stages: Rental', value: 'R', disabled: true },
