  { label: '13 R3D: Collect 3 months deposit', value: 'R3D' },
  { label: '14 RKY: Pass key to tenant', value: 'RKY' },
  { label: '15 RST: Landowner pays SST', value: 'RST2' },  // Changed value to RST2 to avoid duplicates
  { label: '16 RCP: Claim and wait for payment', value: 'RCP' },
  { label: '17 RE: Rental process completed', value: 'RE' },

  // Viewing & Closing Stages
  { label: '-Viewing & Closing Stages', value: 'C', disabled: true },
  { label: '1 CA: Viewing appointment', value: 'CA' },
  { label: '2 CF: Follow-up after viewing', value: 'CF' },
  { label: '3 CN: Price negotiation', value: 'CN' },
  { label: '4 CP: Payment and signing', value: 'CP' },
  { label: '5 CAP: Awaiting payment', value: 'CAP' },
  { label: '6 CFN: Final negotiation', value: 'CFN' },
  { label: '7 CCD: Completed deal', value: 'CCD' },

  // Additional Statuses
  { label: '-Additional Statuses', value: 'A', disabled: true },
  { label: '1 ANI: Client not interested', value: 'ANI' },
  { label: '2 AUN: Unsuccessful deal', value: 'AUN' },
  { label: '3 ADC: Deal canceled', value: 'ADC' },
  { label: '4 AXX: Do not contact', value: 'AXX' },
  { label: '5 AFI: Follow-up incomplete', value: 'AFI' },
  { label: '6 ACB: Callback requested', value: 'ACB' },
  { label: '7 AP: Pending status', value: 'AP' },
];


    // Set the initial localStage based on selectedStage prop
    this.localStage = this.findStageOption(this.selectedStage);
  },
  watch: {
    selectedStage(newStage) {
      this.localStage = this.findStageOption(newStage); // Update localStage to match the new prop value
    },
  },
  methods: {
    findStageOption(value) {
      // Find and return the corresponding stage option object based on the value
      return this.stageOptions.find(option => option.value === value) || null;
    },
    emitStageChange() {
      // Emit the selected stage value back to the parent
      this.$emit('update-stage', this.localStage ? this.localStage.value : null);
    },
    getStageDescription(value) {
      // Use findStageOption to get the option, then return the label (description)
      const option = this.findStageOption(value);
      return option ? option.label : null;
    },
  },
};
</script>

