export function convertYMD2DMY(dateString) {
  // Check if dateString is valid
  if (!dateString) return null;

  // Parse the date string to a Date object
  const date = new Date(dateString);

  // Check if the date is the default invalid date (1900-01-01)
  if (date.getFullYear() === 1900 && date.getMonth() === 0 && date.getDate() === 1) {
    return null;
  }

  // Format the date as DD/MM/YYYY
  const dayStr = String(date.getDate()).padStart(2, '0');
  const monthStr = String(date.getMonth() + 1).padStart(2, '0'); // Months are zero-based
  const yearStr = date.getFullYear();

  return `${dayStr}/${monthStr}/${yearStr}`;
}
// Update a contact
export async function updateContact(id, updatedData) {
  // Check if advUrlDate exists and is valid before converting it
  if (updatedData.advUrlDate !== undefined && updatedData.advUrlDate !== null && updatedData.advUrlDate !== "") {
    updatedData.advUrlDate = convertDMY2YMD(updatedData.advUrlDate);
  } else {
    // Optionally, you can choose to remove advUrlDate if it's undefined/null
    delete updatedData.advUrlDate; // This will omit the field from the mutation if it's undefined
  }

  if (updatedData.dialedDate !== undefined && updatedData.dialedDate !== null && updatedData.dialedDate !== "") {
    updatedData.dialedDate = convertDMY2YMDtime(updatedData.dialedDate);
  } else {
    // Optionally, you can choose to remove advUrlDate if it's undefined/null
    delete updatedData.dialedDate; // This will omit the field from the mutation if it's undefined
  }

  
  const mutation = gql`
    mutation updateLead($id: ID!, $data: LeadInput!) {
      updateLead(id: $id, data: $data) {
        data {
          id
          attributes {
            name
            phoneNumber
            project
            called
            contacted
            phoneId
            clientType  
            process
            stage
            task
            delegate
            address     
            advUrl         
            advUrlDate     
            facebookUrl
            notionUrl
          }
        }
      }
    }
  `;

  try {
    const { data } = await client.mutate({
      mutation,
      variables: {
        id,
        data: updatedData,
      },
    });

    return data.updateLead.data;
  } catch (error) {
    alert("error 138");
    alert(error.cause); 
    console.error('Error updating contact:', error.cause);
    throw error;
  }
}



