<template>
    <div>
      <h1>Process Selection</h1>
  
      <!-- Process Selection -->
      <div>
        <label for="process-select">Select Process:</label>
        <select
          v-model="selectedProcess"
          id="process-select"
          @update:modelValue="handleProcessChange"
          style="border-bottom: 1px solid #ccc;"
        >
          <option
            v-for="process in processOptions"
            :key="process.value"
            :value="process.value"
          >
            {{ process.label }}
          </option>
        </select>
      </div>
  
      <!-- Stage Selection (conditional) -->
      <div v-if="filteredStages.length > 0">
        <label for="stage-select">Select Stage:</label>
        <select v-model="selectedStage" id="stage-select" style="border-bottom: 1px solid #ccc;">
          <option
            v-for="stage in filteredStages"
            :key="stage.value"
            :value="stage.value"
          >
            {{ stage.label }}
          </option>
        </select>
      </div>
  
      <!-- Task Selection (conditional) -->
      <div v-if="filteredTasks.length > 0">
        <label for="task-select">Select Task:</label>
        <select v-model="selectedTask" id="task-select" style="border-bottom: 1px solid #ccc;">
          <option
            v-for="task in filteredTasks"
            :key="task.value"
            :value="task.value"
          >
            {{ task.label }}
          </option>
        </select>
      </div>
  
      <!-- ProcessSelector component -->
      <ProcessSelector
        @update:selectedProcess="handleSelectedProcessChange"
        @update:selectedStage="handleSelectedStageChange"
        @update:selectedTask="handleSelectedTaskChange"
      />
  
      <!-- StageSelector component -->
      <StageSelector
        @update:selectedStage="handleSelectedStageChange"
        @update:selectedTask="handleSelectedTaskChange"
      />
    </div>
  </template>
  

