                                { label: 'Staging the home to make it attractive to buyers.', value: 'hps7' },
                                { label: 'Take professional photos and create a sales listing.', value: 'hpt8' },
                                { label: '*Post photo to notion, and copy the link to Property Manager', value: 'hpp9' },
                                { label: '*Post Video to facebook, and copy the link to Property Manager', value: 'hpv10' },
                                { label: 'Get Info on Lease Hold, Freehold, Landsize, Builup Area, no of Room, bath room, car par', value: 'hpt11' },
