                                { label: '*Collecting payment and any additional fees.', value: 'hpc2' },
                                { label: 'Handling any post-sale issues or questions from the buyer.', value: 'hph3' },
                                { label: '*Handling of Vaccant Procession.', value: 'hph4' },
                            ],
                        },
                    ],
                },
                {
                    label: 'Co-Broke Agents Collaborating',
                    value: 'co-broke-agents',
                    stages: [
                        {
