                            label: 'Property Listing and Marketing',
                            value: 'property-listing',
                            tasks: [
                                { label: 'Share property listings and details with each other.', value: 'cps1' },
                                { label: '*Get Property info', value: 'cpi2' },
                                { label: '*Post photo to notion, and copy the link to Property Manager', value: 'cpp3' },
                                { label: '*Post Video to facebook, and copy the link to Property Manager', value: 'cpv4' },

                                { label: '*Make sure the other party not using iproperty.', value: 'cpm5' },
                                { label: '*Decide the advertisment price, make sure it not highier then others', value: 'cpd6' },
                                { label: '*Create record in "PM", and get refering No to place in Advertising Refer field', value: 'cpd7' },
                                { label: '*Plase Advertising URL to "PM"', value: 'cpd8' },
                                { label: '*Please Broker name and Phone to "PM"', value: 'cpd9' },
                                { label: '*Please Advertisement Date in "PM"', value: 'cpd10' },

                                { label: 'Jointly market the property through combined resources. mudah,property guru,iproperty,nextsix', value: 'cpj7' },
                                { label: 'Coordinate open houses and property viewings.', value: 'cpc8' },
                                
                            ],
                        },
                        {
                            label: 'Lead Management',
                            value: 'lead-management',
                            tasks: [
                                { label: '*If our lead, Share leads and client information with co-broke agents.', value: 'cls1' },
                                { label: 'Collaborate on follow-up with potential buyers or sellers.', value: 'clc2' },
                                { label: 'Track leads and manage inquiries collaboratively.', value: 'clt3' },
