                                { label: 'Coordinate efforts for property inspections and appraisals.', value: 'csc2' },
                                { label: '*Finalize agreements and contracts together.', value: 'csf3' },
                            ],
                        },
                        {
                            label: 'Post-Sale Follow-Up',
                            value: 'post-sale-followup',
                            tasks: [
                                { label: 'Share feedback and insights on the collaboration.', value: 'cps1' },
                                { label: 'Discuss potential future collaborations.', value: 'cps2' },
                                { label: '*Maintain a network for ongoing communication.', value: 'cps3' },
