                },

                {
                    label: 'Others',
                    value: 'Others',
                    stages: [

                        {
                            label: '-',
                            value: '-',
                            tasks: [
                                { label: 'Client not interested', value: 'ni' },
                                { label: 'Unsuccessful deal', value: 'xdeal' },
                                { label: 'Pending', value: 'pending' },
                   ],
                    },
                ],
            },
        ]);

        

        function handleSelectedProcessChange(value) {
            console.log('Selected process:', value);
        }

        function handleSelectedStageChange(value) {
            console.log('Selected stage:', value);
        }

        function handleSelectedTaskChange(value) {
            console.log('Selected task:', value);
        }

        function handleProcessChange() {
            selectedStage.value = null;
            selectedTask.value = null;
            emit('update:selectedProcess', selectedProcess.value);
        }

        function handleStageChange() {
            selectedTask.value = null;
            emit('update:selectedStage', selectedStage.value);
        }

        function handleTaskChange() {
            emit('update:selectedTask', selectedTask.value);
        }

        watch(selectedProcess, () => {
            handleProcessChange();
        });

        watch(selectedStage, () => {
            handleStageChange();
        });

        watch(selectedTask, () => {
            handleTaskChange();
        });
        const filteredStages = computed(() => {
            const selectedProcessData = processOptions.value.find(option => option.value === selectedProcess.value);
            return selectedProcessData ? selectedProcessData.stages : [];
        });

        const filteredTasks = computed(() => {
            const selectedStageData = filteredStages.value.find(stage => stage.value === selectedStage.value);
            return selectedStageData ? selectedStageData.tasks : [];
        });


        return {
            selectedProcess,
            selectedStage,
            selectedTask,
            processOptions,
            filteredStages,
            filteredTasks,
            handleProcessChange,
            handleStageChange,
            handleTaskChange,

            handleSelectedProcessChange,

            handleSelectedStageChange,

            handleSelectedTaskChange
        };
    },
};
</script>

