<template>
    <v-dialog v-model="internalDialogVisible" persistent max-width="290">
      <v-card>
        <v-card-title>Select Status</v-card-title>
        <v-card-text>
          <v-select 
            v-model="internalSelectedStatus" 
            :items="statusOptions" 
            label="Status" 
            outlined
          />
        </v-card-text>
        <v-card-actions class="d-flex justify-space-between">
          <v-btn text @click="confirmStatus">Confirm</v-btn>
          <v-btn text @click="closeDialog">Cancel</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </template>
  
  <script>
  import { ref, watch, defineComponent } from 'vue';
  
  export default defineComponent({
    name: 'statusDialog',
    props: {
      showDialog: {
        type: Boolean,
        required: true
      },
      selectedStatus: {
        type: String,
        default: ''
      }
    },
    emits: ['update:showDialog', 'updateStatus'],
    setup(props, { emit }) {
      // Reactive properties
      const internalDialogVisible = ref(props.showDialog);
      const internalSelectedStatus = ref(props.selectedStatus);
  
      const statusOptions = ['', 'ni', 'nis', 'eg', 'XXX', 'nr', 'ua'];
  
      // Watch for prop changes and sync with internal state
      watch(() => props.showDialog, (newVal) => {
        internalDialogVisible.value = newVal;
      });
  
      watch(() => props.selectedStatus, (newVal) => {
        internalSelectedStatus.value = newVal;
      });
  
      // Watch internal dialog visibility and emit back to parent
      watch(internalDialogVisible, (newVal) => {
        emit('update:showDialog', newVal);
      });
  
      const confirmStatus = () => {
        emit('updateStatus', internalSelectedStatus.value);
        closeDialog();
      };
  
      const closeDialog = () => {
        internalDialogVisible.value = false;
      };
  
      return {
        internalDialogVisible,
        internalSelectedStatus,
        statusOptions,
        confirmStatus,
        closeDialog
      };
    }
  });
  </script>
  