<template>

  <h1 id="registration-form">Registration Form</h1>
  <div class="form-container">
    <h3>Fill up your details and we would be delighted to assist you</h3>
    <form @submit.prevent="submitForm">
      <div class="form-group">
        <label v-if="localPackage" for="project">Package Name:</label>
        <input class="white-text" type="text" id="package" v-model="localPackage" name="package-name" placeholder="Package*" required>
      </div>
      <div class="form-group">
        <label v-if="name" for="name">Name:</label>
        <input class="white-text" type="text" id="name" v-model="name" placeholder="Name*" required>
      </div>
      <div class="form-group">
        <label v-if="phone" for="phone">Phone:</label>
        <input class="white-text" type="tel" id="phone" v-model="formattedPhone" @input="formatPhone" placeholder="Phone*" required>
      </div>
      <div class="form-group">
        <label v-if="email" for="email">Email:</label>
        <input class="white-text" type="email" id="email" v-model="email" placeholder="Email*" required>
      </div>
      <div class="form-group">
        <label v-if="address" for="address">Installation Address:</label>
        <input class="white-text" type="text" id="address" v-model="address" placeholder="Installation Address*" required>
      </div>
      <div class="form-group">
        <label v-if="message" for="message">Additional Info:</label>
        <textarea class="white-text" id="message" v-model="message" placeholder="Additional Info*" required></textarea>
      </div>
      <label for="appointmentDate">Installation Schedule:</label>
      <div class="form-group">
        <div class="form-row">
          <input class="white-text" type="date" id="appointmentDate" v-model="appointmentDate" placeholder="Appointment Date*" required>
          <input class="white-text" type="time" id="appointmentTime" v-model="appointmentTime" placeholder="Appointment Time*" required>
        </div>
      </div>
      <div class="form-group">
        <label for="preferredInstallationDate">Preferred Installation Date (dd/mm/yyyy):</label>
        <input class="white-text" type="text" id="preferredInstallationDate" v-model="preferredInstallationDate" placeholder="Preferred Installation Date*" required>
      </div>
      <div class="form-group">
        <label for="nationality">Nationality:</label>
        <input class="white-text" type="text" id="nationality" v-model="nationality" placeholder="Nationality*" required>
      </div>
      <div class="form-group">
        <label for="icFront">IC Front:</label>
        <input type="file" id="icFront" @change="handleFileUpload($event, 'icFront')" required>
        <p>{{ icFrontName || 'No file chosen' }}</p>
      </div>
      <div class="form-group">
        <label for="icBack">IC Back:</label>
        <input type="file" id="icBack" @change="handleFileUpload($event, 'icBack')" required>
        <p>{{ icBackName || 'No file chosen' }}</p>
      </div>
      <div class="form-group">
        <label for="utilityBills">Utility Bills (Electric or Water):</label>
        <input type="file" id="utilityBills" @change="handleFileUpload($event, 'utilityBills')" required>
        <p>{{ utilityBillsName || 'No file chosen' }}</p>
      </div>
      <popUp2 :is-visible="showModal" :modal-message="modalMessage" @close="showModal = false" />

      <button type="submit" class="submit-button white-text" :disabled="formSubmitted">
        {{ formSubmitted ? 'Submitting...' : 'Yes! Contact Me Now' }}
      </button>
    </form>
    <br/>
    <TermsConditions/>
  </div>
  <popUp2 :is-visible="showModal" :modal-message="modalMessage" @close="showModal = false" />
  <!-- <button type="submit" class="submit-button white-text" :disabled="formSubmitted">
    {{ formSubmitted ? 'Submitting...' : 'Yes! Contact Me Now' }}
  </button> -->
</template>

<script>
import { ref, watch } from 'vue';
import { useMutation, useQuery } from '@vue/apollo-composable';
import { gql } from '@apollo/client/core';
import popUp2 from './popUp2.vue'; // Path to your modal component

export default {
  name: 'tmReg',
  components: {
    popUp2,
  },
  props: {
    project: {
      type: String,
      required: true,
    },
  },
  emits: ['close'],
  setup(props, { emit }) {
    const localProject = ref(props.project);
    const localPackage = ref('');
    const formSubmitted = ref(false);
    const showModal = ref(false);
    const modalMessage = ref('');
    const name = ref('');
    const phone = ref('');
    const email = ref('');
    const address = ref('');
    const message = ref('');
    const appointmentDate = ref('');
    const appointmentTime = ref('');
    const preferredInstallationDate = ref('');
    const nationality = ref('');
    const icFront = ref(null);
    const icBack = ref(null);
    const utilityBills = ref(null);
    const icFrontName = ref('');
    const icBackName = ref('');
    const utilityBillURL = ref('');
    const utilityBillsName = ref('');

    const formatPhone = (event) => {
      const input = event.target;
      const trimmedValue = input.value.replace(/\D/g, '').substring(0, 10);
      const formattedValue = trimmedValue.replace(/(\d{3})(\d{3})(\d{4})/, '($1) $2-$3');
      input.value = formattedValue;
      phone.value = trimmedValue;
    };

    watch(() => props.project, (newVal) => {
      localProject.value = newVal;
    });

    const handleFileUpload = (event, fileType) => {
      const file = event.target.files[0];
      if (fileType === 'icFront') {
        icFront.value = file;
        icFrontName.value = file.name;
      } else if (fileType === 'icBack') {
        icBack.value = file;
        icBackName.value = file.name;
      } else if (fileType === 'utilityBills') {
        utilityBills.value = file;
        utilityBillURL.value = file.name;
      }
    };

    const close = () => {
      emit('close');
    };

    const CHECK_CONTACT = gql`
      query ($phone: String) {
        contacts(filters: { phone: { eq: $phone } }) {
          data {
            id
            attributes {
              name
              phone
            }
          }
        }
      }
    `;

    const ADD_CONTACT = gql`
  mutation AddContact($name: String!, $project: String!, $email: String!,$package:String!,
  $phone: String!, $address: String!, $message: String!, $PreferredInstallationDate: String!, 
  $Nationality: String!, $ICFrontURL: String!, $ICBackURL: String!, $UtilityBillURL: String!) {
  createContact(data: { 
    project: $project, 
    package: $package,
    name: $name, 
    email: $email, 
    phone: $phone, 
    installationAddress: $address, 
    message: $message, 
    PreferredInstallationDate: $PreferredInstallationDate, 
    Nationality: $Nationality, 
    ICFrontURL: $ICFrontURL, 
    ICBackURL: $ICBackURL, 
    UtilityBillURL: $UtilityBillURL 
  }) {
    data {
      id
      attributes {
        project
        package
        name
        phone
        IC
        email
        installationAddress
        billingAddress
        message
        PreferredInstallationDate
        Nationality
        ICFrontURL
        ICBackURL
        UtilityBillURL 
      }
    }
  }
}
 `;

 

    const UPDATE_CONTACT = gql`
     mutation UpdateContact($id: ID!, $name: String!, $project: String!, $email: String!, $package: String!, 
  $phone: String!, $address: String!, $message: String!, $PreferredInstallationDate: String!, 
  $Nationality: String!, $ICFrontURL: String!, $ICBackURL: String!, $UtilityBillURL: String!) {
  updateContact(id: $id, data: { 
    project: $project, 
    package: $package,
    name: $name, 
    email: $email, 
    phone: $phone, 
    installationAddress: $address, 
    message: $message, 
    PreferredInstallationDate: $PreferredInstallationDate, 
    Nationality: $Nationality, 
    ICFrontURL: $ICFrontURL, 
    ICBackURL: $ICBackURL, 
    UtilityBillURL: $UtilityBillURL 
  }) {
    data {
      id
      attributes {
        project
        package
        name
        phone
        IC
        email
        installationAddress
        billingAddress
        message
        PreferredInstallationDate
        Nationality
        ICFrontURL
        ICBackURL
        UtilityBillURL 
      }
    }
  }
}

    `;

    const { mutate: addContact } = useMutation(ADD_CONTACT);
    const { mutate: updateContact } = useMutation(UPDATE_CONTACT);

    const { refetch: refetchContact, loading: checkContactLoading, error: checkContactError } = useQuery(CHECK_CONTACT, {
      variables: { phone: phone.value },
      skip: true,
    });

    const submitForm = async () => {
      formSubmitted.value = true;
      try {
        const { data } = await refetchContact({ phone: phone.value });

        if (checkContactError.value) {
          console.error('Error fetching contact:', checkContactError.value);
          return;
        }

        if (checkContactLoading.value) {
          console.log('Waiting for contact check...');
          return;
        }

        if (!data.contacts.data || data.contacts.data.length === 0) {
          console.log('Contact does not exist. Creating new contact.');
          const response = await addContact({
            package: localPackage.value,
            project: localProject.value,
            name:name.value,
            phone: phone.value,
            email: email.value,
            address: address.value,
            message: message.value,
            PreferredInstallationDate: preferredInstallationDate.value,
            Nationality: nationality.value,
            ICFrontURL: "icFront.value",
            ICBackURL: "icBack.value",
            UtilityBillURL: "utilityBillURL.value"




          });
          console.log('Create Success:', response);
          formSubmitted.value = false;
          showModal.value = true;
          modalMessage.value = 'Record created.';
          return;
        }

        const contact = data.contacts.data[0];

        console.log('Contact exists. Updating contact with ID:', contact.id);
        const response = await updateContact({
          id: contact.id,
            project: localProject.value,
            package: localPackage.value,
            name:name.value,
            phone: phone.value,
            email: email.value,
            address: address.value,
            message: message.value,
            PreferredInstallationDate: preferredInstallationDate.value,
            Nationality: nationality.value,
            ICFrontURL: "icFront.value",
            ICBackURL: "icBack.value",
            UtilityBillURL: "utilityBillURL.value"

        });
        console.log('Update Success:', response);
        formSubmitted.value = false;
        showModal.value = true;
        modalMessage.value = 'Record updated.';
      } catch (error) {
        formSubmitted.value = false;
        console.error('Error:', error);
      }
    };

    return {
      localPackage,
      localProject,
      name,
      phone,
      email,
      address,
      message,
      appointmentDate,
      appointmentTime,
      preferredInstallationDate,
      nationality,
      icFront,
      icBack,
      utilityBills,
      icFrontName,
      icBackName,
      utilityBillsName,
      formSubmitted,
      submitForm,
      formatPhone,
      showModal,
      modalMessage,
      close,
      handleFileUpload
    };
  },
};
</script>


<style scoped>
.form-row {
  display: flex;
  justify-content: space-between;
}
.form-container {
  background-image: url('https://img.freepik.com/free-vector/copy-space-bokeh-spring-lights-background_52683-55649.jpg?t=st=1722214293~exp=1722214893~hmac=3e6d770121f17a03627399923a9f115070bc2a69cda67ca74a935169dcba1291');
  background-size: cover;
  background-position: center;
  padding: 20px;
  border-radius: 8px;
}

h1 {
  text-align: center;
  color: #000000;
}

h3 {
  text-align: center;
  color: #000000;
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #000000;
}

input[type="text"],
input[type="tel"],
input[type="email"],
input[type="project"],
input[type="date"],
input[type="time"],
textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

textarea {
  resize: vertical;
}

.submit-button {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  font-size: 16px;
  cursor: pointer;
}

.white-text {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  color: rgb(10, 0, 0); /* Set text color to white */
}

.submit-button:hover {
  background-color: #0056b3;
}
</style>

